import React, {useState, useEffect, useRef, createRef} from 'react'
import {useHistory,useLocation, Redirect  } from "react-router-dom";
import classes from './character-page.module.scss'

import data from '../../data/data.js'
import items from '../../data/scorpion.json'

const menuItems = [
  {menuTitle:'Комбинации', position:0,iWidth:112},
  {menuTitle:'Удары',position:112,iWidth:69},
  {menuTitle:'Серии',position:147,iWidth:66},
  {menuTitle:'Спецприемы',position:125,iWidth:111},
  {menuTitle:'Фаталити',position:135,iWidth:91},
  {menuTitle:'Бруталити',position:132,iWidth:97},
  {menuTitle:'Сокрушительный удар',position:182, iWidth:178}
]



const compWidth = 360;

let firstClientX, clientsX, firstClientY, clientsY;

const Scrollable = ({match, headerSw}) => {

  // const items = props.items;
  const location = useLocation();

  let ref = useRef();
  let menuRef = useRef();
  let lineRef = useRef();

  let refAnimation = useRef();
  let menuAnimation = useRef();
  let sliderAnimation = useRef();
  let lineAnim = useRef();
  let widthAnimat = useRef();

  const [state, setState] = useState({
    isScrolling: false,
    clientX: 0, //Position of the mouse on the object
    scrollX: 0, //Position of the propelled object
    isScrollingM: false,
    clientM: 0,
    scrollM: 0,
    clientL:0,
    scrollL:0,
    clientW:0,
    scrollW:0
  });
  const [menuLastPos, setLastPos] = useState(0)
  const [stateDir, setDir] = useState(0)
  const [touchStart, setTouchStart] = useState(0);
  const [animState, setAnimState] = useState(false);
  const [mouseDown, setMouseDown] = useState(false);
  const [touchStartMenu, setTouchStartMenu] = useState(0);
  const [sliderIndex, setSliderIndex] = useState(0);
  const [linePosition, setLinePosition] = useState(0);
  const [lastWidth, setLastWidth] = useState(112);

  const onMouseDown = (e) => {


        if (ref && ref.current && !ref.current.contains(e.target)) {
          return;
        }
        e.preventDefault();
            if(animState === false) {

                setMouseDown(true)
                setTouchStart(e.clientX);

                setState({
                  ...state,
                  isScrolling: true,
                  clientX: e.clientX,
                  clientM: e.clientX,
                  clientL: e.clientX,
                  clientW: e.clientX,
                  scrollW:0
                });

            } else if(animState === true) {
              setMouseDown(false)
            }

  };

  const onMouseMove = (e) => {

    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    if(animState === false) {

    const { clientX, scrollX, isScrolling, scrollM, clientM, clientL, scrollL, scrollW, clientW } = state;

    if (isScrolling === true) {

          let sX = scrollX - e.clientX + clientX;
          let cX = e.clientX;
          let sM;
          let cM = e.clientX;
          let sL;
          let cL = e.clientX;
          let sW;
          let cW = e.clientX;


          if (sX < -0) {
            sX = 0;
          } else if (sX > 2160) {
            sX = 2160;
          }
          else if(sX >= 0 && sX <= 2160) {
              ref.current.scrollLeft = sX;


              if (touchStart - e.clientX > 0) {

                  let width = menuItems[sliderIndex + 1].iWidth

                  let sum = 0;
                  for(let i = 0; i < sliderIndex + 1; i++){
                    sum = sum + menuItems[i].iWidth
                  }
                  let position = (sum - (compWidth - width) / 2) - menuLastPos
                  let ratio = (compWidth/position).toFixed(3);

                  sM = scrollM - Math.round(e.clientX / ratio) + Math.round(clientM / ratio);

                  ///////////////////Сдвиг влевао////////////////////

                  let nextSlidePos = 0;
                  for(let i = 0; i < sliderIndex + 1; i++){
                    nextSlidePos = nextSlidePos + menuItems[i].iWidth
                  }

                 let nextWidth =  menuItems[sliderIndex + 1].iWidth/2
                 let curW = menuItems[sliderIndex].iWidth
                 let rez;
                 let sumZ = 0;
                 let ratioL;

                 for(let i = 0; i < sliderIndex; i++) {
                   sumZ += menuItems[i].iWidth;
                 }

                 rez = sumZ - (compWidth - menuItems[sliderIndex].iWidth) / 2;

                  if(nextSlidePos <= compWidth/2) {
                        ratioL = (compWidth/curW).toFixed(3);
                        sL = scrollL - (e.clientX / ratioL) + (clientL / ratioL);
                        lineRef.current.style.marginLeft = sL + 'px';
                  }

                  else if (nextSlidePos > compWidth/2 && (375 - rez) > nextWidth){
                    let newPos = (compWidth - menuItems[sliderIndex + 1].iWidth)/2;
                    let dif = linePosition - newPos;
                    ratioL = Math.abs(compWidth/dif)

                    if(dif > 0) {
                      sL = scrollL + (e.clientX / ratioL) - (clientL / ratioL);
                    }
                    if(dif < 0) {
                      sL = scrollL - (e.clientX / ratioL) + (clientL / ratioL);
                    }
                    lineRef.current.style.marginLeft = sL + 'px';
                  }

                  else if (nextSlidePos > compWidth/2 && (375 - rez) < nextWidth) {
                    let newPos = compWidth - menuItems[sliderIndex + 1].iWidth
                    let dif = linePosition - newPos;
                    ratioL = Math.abs(compWidth/dif)

                    if(dif > 0) {
                      sL = scrollL + (e.clientX / ratioL) - (clientL / ratioL);
                    }
                    if(dif < 0) {
                      sL = scrollL - (e.clientX / ratioL) + (clientL / ratioL);
                    }

                    lineRef.current.style.marginLeft = sL + 'px';
                  }

                  //////////////////Изменение ширины////////////////////

                  let curWidth = menuItems[sliderIndex].iWidth
                  let nextItemW = menuItems[sliderIndex + 1].iWidth
                  let widthDif = curWidth - nextItemW;
                  let widthRatio = compWidth/ widthDif;

                  if(widthDif > 0) {
                    sW = scrollW - (e.clientX/ widthRatio) + (clientW/ widthRatio);
                    lineRef.current.style.width = curWidth - sW + 'px';
                  } else if (widthDif < 0) {
                    sW = scrollW + (e.clientX/ widthRatio) - (clientW/ widthRatio);
                    lineRef.current.style.width = curWidth + sW + 'px';
                  }

                  setLastWidth(parseFloat(lineRef.current.style.width))


                  //////////////////////////////////////////////////////

                  if (sM < 0) {
                    sM = 0;
                  } else if (sM > 364) {
                    sM = 364;
                  } else if (sM > 0 && sM <= 364) {
                      menuRef.current.scrollLeft = sM;
                  }

                  setState({
                    ...state,
                    scrollX: sX,
                    clientX: cX,
                    scrollM: sM,
                    clientM: cM,
                    scrollL: sL,
                    clientL: cL,
                    clientW: cW,
                    scrollW: sW,
                  });

              }
              else if (touchStart - e.clientX < 0) {

                  let width = menuItems[sliderIndex - 1].iWidth
                  let sum = 0;
                  for(let i = 0; i < sliderIndex - 1; i++){
                    sum = sum + menuItems[i].iWidth
                  }
                  let position = menuLastPos - (sum - (compWidth - width) / 2)
                  let ratio = (compWidth/position).toFixed(3);

                  sM = scrollM - Math.round(e.clientX / ratio) + Math.round(clientM / ratio);
                  //////////////////Сдвиг вправо////////////////////

                  let nextSlidePos = 0;
                  for(let i = 0; i < sliderIndex - 1; i++){
                    nextSlidePos = nextSlidePos + menuItems[i].iWidth
                  }


                 let ratioL;

                  if(nextSlidePos <= compWidth/2) {
                        let prevPos =  linePosition - nextSlidePos
                        ratioL = (compWidth/prevPos).toFixed(3);
                        sL = scrollL - (e.clientX / ratioL) + (clientL / ratioL);
                        lineRef.current.style.marginLeft = sL + 'px';

                  }

                  else if (nextSlidePos > compWidth/2){
                    let newPos = (compWidth - menuItems[sliderIndex - 1].iWidth)/2;
                    let dif = linePosition - newPos;
                    ratioL = Math.abs(compWidth/dif)

                    if(dif < 0) {
                      sL = scrollL + (e.clientX / ratioL) - (clientL / ratioL);
                    }
                    if(dif > 0) {
                      sL = scrollL - (e.clientX / ratioL) + (clientL / ratioL);
                    }
                    lineRef.current.style.marginLeft = sL + 'px';
                  }



                    let curWidth = menuItems[sliderIndex].iWidth
                    let nextItemW = menuItems[sliderIndex - 1].iWidth
                    let widthDif = curWidth - nextItemW;
                    let widthRatio = compWidth/ widthDif;

                    if(widthDif > 0) {
                      sW = scrollW + (e.clientX/ widthRatio) - (clientW/ widthRatio);
                      lineRef.current.style.width = curWidth - sW + 'px';
                    } else if (widthDif < 0) {
                      sW = scrollW - (e.clientX/ widthRatio) + (clientW/ widthRatio);
                      lineRef.current.style.width = curWidth + sW + 'px';
                    }

                    setLastWidth(parseFloat(lineRef.current.style.width))


                  //////////////////////////////////////////////////////

                  if (sM < 0) {
                    sM = 0;
                  }  else if (sM > 364) {
                    sM = 364;
                  }  else if (sM > 0 && sM <= 364) {
                      menuRef.current.scrollLeft = sM;
                  }
                  setState({
                    ...state,
                    scrollX: sX,
                    clientX: cX,
                    scrollM: sM,
                    clientM: cM,
                    scrollL: sL,
                    clientL: cL,
                    clientW: cW,
                    scrollW: sW,
                  });

              }
          }
    }
  }

  };

  const onMouseUp = (e) => {

    if(mouseDown === true) {
      if (ref && ref.current && !ref.current.contains(e.target)) {
        return;
      }
      e.preventDefault();

      // if(animState == false)
        let touchShift = touchStart - state.clientX;
        shiftDirection(touchShift)
        // setLinePosition(state.scrollL) // del временно
      // }
    }
  };

  const shiftDirection = (touchShift) => {

    if (touchShift <= -80) {

        let sum = 0;
        for(let i = 0; i < sliderIndex - 1; i++) {
          sum += menuItems[i].iWidth;
        }
        let rez2 = Math.round(sum - (compWidth - menuItems[sliderIndex -1].iWidth) / 2);

        if(rez2 < -0){
          rez2 = 0
        } else if (rez2 > 364){
          rez2 = 364
        }

        cancelAnimationFrame(menuAnimation.current);
        menuAnimation.current = requestAnimationFrame(() =>  menuAnim('left',rez2));

        let rez3 = menuItems[sliderIndex - 1].position
        let cur = state.scrollL;
        let dif = cur - rez3;
        let speed = Math.abs(dif/15)
        cancelAnimationFrame(lineAnim.current);
        lineAnim.current = requestAnimationFrame(() =>  animatLine(speed,cur,rez3,dif));

        let nextItemW = menuItems[sliderIndex -1].iWidth
        cancelAnimationFrame(widthAnimat.current);
        widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));

        let  shift = compWidth + touchShift;
        let  rez = state.scrollX - shift;

        anim(rez, shift, 15, "left", rez2, rez3, nextItemW)

        if(sliderIndex >0){
          setSliderIndex(sliderIndex - 1)
        }
    }
    if (touchShift < 0 && touchShift > -80) {



        let sum = 0;
        for(let i = 0; i < sliderIndex; i++) {
          sum += menuItems[i].iWidth;
        }
        let rez2 = Math.round(sum - (compWidth - menuItems[sliderIndex].iWidth) / 2);
        if(rez2 < -0){
          rez2 = 0
        } else if (rez2 > 364){
          rez2 = 364
        }



        cancelAnimationFrame(menuAnimation.current);
        menuAnimation.current = requestAnimationFrame(() =>  menuAnim('right',rez2));

        let rez3 = menuItems[sliderIndex].position
        let cur = state.scrollL;
        let dif = cur - rez3;
        let speed = Math.abs(dif/20)
        cancelAnimationFrame(lineAnim.current);
        lineAnim.current = requestAnimationFrame(() =>  animatLine(speed,cur,rez3,dif));

        let nextItemW = menuItems[sliderIndex].iWidth
        cancelAnimationFrame(widthAnimat.current);
        widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));

        let  shift = touchShift * -1;
        let  rez = state.scrollX + shift;

        anim(rez, shift, 20, "right", rez2, rez3,nextItemW)



    }
    if (touchShift >= 80) {
      let sum = 0;
      for(let i = 0; i < sliderIndex + 1; i++) {
        sum += menuItems[i].iWidth;
      }
      let rez2 = Math.round(sum - (compWidth - menuItems[sliderIndex + 1].iWidth) / 2);
      if(rez2 < -0){
        rez2 = 0
      } else if (rez2 > 364){
        rez2 = 364
      }

      cancelAnimationFrame(menuAnimation.current);
      menuAnimation.current = requestAnimationFrame(() =>  menuAnim('right',rez2));

      let rez3 = menuItems[sliderIndex + 1].position
      let cur = state.scrollL;
      let dif = cur - rez3;
      let speed = Math.abs(dif/15)


      animatLine(speed,cur,rez3,dif)

      let nextItemW = menuItems[sliderIndex + 1].iWidth
      cancelAnimationFrame(widthAnimat.current);
      widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));


      let  shift = compWidth - touchShift;
      let  rez = state.scrollX + shift;


      anim(rez, shift, 15, "right", rez2, rez3,nextItemW)
      if(sliderIndex < 6){
        setSliderIndex(sliderIndex + 1)
      }

      }
    if (touchShift < 80 && touchShift > 0) {


      let sum = 0;
      for(let i = 0; i < sliderIndex; i++) {
        sum += menuItems[i].iWidth;
      }
      let rez2 = Math.round(sum - (compWidth - menuItems[sliderIndex].iWidth) / 2);
      if(rez2 < -0){
        rez2 = 0
      } else if (rez2 > 364){
        rez2 = 364
      }

      cancelAnimationFrame(menuAnimation.current);
      menuAnimation.current = requestAnimationFrame(() =>  menuAnim('left',rez2));

        let rez3 = menuItems[sliderIndex].position
        let cur = state.scrollL;
        let dif = cur - rez3;
        let speed = Math.abs(dif/20)
        cancelAnimationFrame(lineAnim.current);
        lineAnim.current = requestAnimationFrame(() =>  animatLine(speed,cur,rez3,dif));

        let nextItemW = menuItems[sliderIndex].iWidth
        widthMove(lastWidth,15, nextItemW)



        let  shift = touchShift;
        let  rez = state.scrollX - shift;

        anim(rez, shift, 20, "left", rez2,rez3, nextItemW)

    }
    if (touchShift === 0) {
        setState({
          ...state,
          isScrolling: false
        });
    }
  }

  const anim = (rez,shift, scor, dir,rez2,rez3, rez4) => {
    let cur = state.scrollX;
    let speed = shift / scor;
    if (rez >= 2520) {
      setState({
        ...state,
        isScrolling: false
      });
    } else if (rez <=-compWidth){
      setState({
        ...state,
        isScrolling: false
      });
    } else {

          cancelAnimationFrame(refAnimation.current);
          refAnimation.current = requestAnimationFrame(() =>  animate(cur, speed, rez, dir, rez2));

              setState({
                  ...state,
                  scrollX: rez,
                  scrollM: rez2,
                  scrollL: rez3,
                  scrollW: rez4,
                  isScrolling: false,
                });

            setLastPos(rez2)
      }
  }

  const animate = (cur, speed, rez, dir) => {
              setAnimState(true);

              refAnimation.current = requestAnimationFrame(() =>
                animate(cur, speed, rez, dir)
              );
              cur = dir === "left" ? cur - speed : cur + speed;
              ref.current.scrollLeft = cur.toFixed(2);

              if(Math.round(cur) === rez) {
              cancelAnimationFrame(refAnimation.current);
              ref.current.scrollLeft = rez;
                setAnimState(false);
                setMouseDown(false);

        }
  };

  const menuAnim = (side, rez2) => {
      let dir;
      if(stateDir === 0) {
        dir = side;
      }else if(stateDir === 'left'){
        dir = stateDir
      } else if (stateDir === 'right') {
        dir = stateDir
      }
      let cur = state.scrollM;
      let speed = Math.abs((cur - rez2) / 20)

      cancelAnimationFrame(menuAnimation.current);
      menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez2,  dir));
  }

  const onMouseLeave = (e) => {

    if (state.isScrolling === true) {
      if(animState === false) {
        if (ref && ref.current && !ref.current.contains(e.target)) {
          return;
        }
            e.preventDefault();
            let touchShift = touchStart - state.clientX;
            shiftDirection(touchShift)
            setLinePosition(state.scrollL)
          }
    }
  };


//////////////////////////////////////////////
  const animatLine = (speed,cur,rez,dif) => {
                lineAnim.current = requestAnimationFrame(() =>
                animatLine(speed,cur,rez,dif));

              if(dif > 0) {
                cur = cur - speed
              }
              if(dif < 0) {
                cur = cur + speed
              }
              lineRef.current.style.marginLeft = cur.toFixed(2) + 'px';

              if(Math.round(cur) === rez) {
                  cancelAnimationFrame(lineAnim.current);
                  lineRef.current.style.marginLeft = rez + 'px';
                  setLinePosition(rez)
              }


  }

  const widthMove = (cur,speed,rez) => {
    widthAnimat.current = requestAnimationFrame(() =>
    widthMove(cur,speed, rez));

    let widthDif = lastWidth - rez;
    let speedW = widthDif/speed;

        if(widthDif > 0) {
          cur = cur - Math.abs(speedW);
        } else if (widthDif < 0) {
          cur = cur + Math.abs(speedW);
        }

      lineRef.current.style.width = cur.toFixed(2) + 'px';

      if(Math.round(cur) === rez) {
          cancelAnimationFrame(widthAnimat.current);
          lineRef.current.style.width = rez + 'px';
      }
  }
/////////////////////////////////////////////

  const onMouseDownHandler = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();
    if(state.isScrollingM === false){
      setTouchStartMenu(e.clientX)
      setState({
        ...state,
        isScrollingM: true,
        clientM: e.clientX
      });
    }
  };

  const onMouseMoveHandler = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    const { clientM, scrollM, isScrollingM } = state;

    if (isScrollingM === true) {
      let sM = scrollM - e.clientX + clientM;
      let cM = e.clientX;

      if (sM < 0) {
        sM = 0;
      } else if (sM > 364) {
        sM = 364;
      } else if (sM >= 0 && sM <= 364) {
        menuRef.current.scrollLeft = sM;
        setState({
          ...state,
          scrollM: sM,
          clientM: cM
        });
        if((menuLastPos - sM) > 0) {
          setDir('right')
        } else if ((menuLastPos - sM) < 0)  {
          setDir('left')
        }
      }
    }
  };

  const onMouseUpHandler = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    setState({
      ...state,
      isScrollingM:false
    });

    let touchShift = touchStartMenu - state.clientM;

    if (touchShift === 0) {
        let cur = state.scrollM;
        let index = Number(e.target.getAttribute('iso'))
        let dir;
        let dirMenu;
        let rez;
        let sum = 0;
        let speed;
        setSliderIndex(index)


        if((sliderIndex - index) < 0) {

          for(let i = 0; i < index; i++) {
            sum += menuItems[i].iWidth;
          }

          rez = Math.round(sum - (compWidth - menuItems[index].iWidth) / 2);
          if(rez < -0){
            rez = 0;
          } else if (rez > 364) {
            rez = 364;
          }

          dir = 'right'
          if((rez - cur) < 0){
            dirMenu = 'left'
          } else if ((rez - cur) > 0) {
            dirMenu = 'right'
          }


          speed = Math.abs((cur - rez) / 20)

          cancelAnimationFrame(menuAnimation.current);
          menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez, dirMenu));

          let cur2 = state.scrollX;
          let rez2 =  compWidth*index
          let speed2 = (rez2 - cur2) /30

          cancelAnimationFrame(refAnimation.current);
          refAnimation.current = requestAnimationFrame(() =>  animateSlider(cur2, speed2, rez2, dir));

          //////////////////////////
          let rez3 = menuItems[index].position
          let curz = state.scrollL;
          let dif = curz - rez3;
          let speed4 = Math.abs(dif/15)

          cancelAnimationFrame(lineAnim.current);
          lineAnim.current = requestAnimationFrame(() =>  animatLine(speed4,curz,rez3,dif));

          let nextItemW = menuItems[index].iWidth
          cancelAnimationFrame(widthAnimat.current);
          widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));
          setLastWidth(nextItemW)

          setState({
            ...state,
            scrollM:rez,
            isScrollingM: false,
            isScrolling:false,
            scrollX:rez2,
            scrollW:nextItemW,
            scrollL:rez3,
          });
        }
        else if ((sliderIndex - index) > 0) {
          for(let i = 0; i < index; i++) {
            sum += menuItems[i].iWidth;
          }

          rez = Math.round(sum - (compWidth - menuItems[index].iWidth) / 2);

          if(rez < -0){
            rez = 0;
          } else if (rez > 364) {
            rez = 364;
          }

          speed = Math.abs((cur - rez) / 20)

          dir = 'left'
          if((rez - cur) < 0){
            dirMenu = 'left'
          } else if ((rez - cur) > 0) {
            dirMenu = 'right'
          }

          cancelAnimationFrame(menuAnimation.current);
          menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez, dirMenu));

          let cur2 = state.scrollX;
          let rez2 =  compWidth*index
          let speed2 = (cur2 - rez2) / 30
          cancelAnimationFrame(refAnimation.current);
          refAnimation.current = requestAnimationFrame(() =>  animateSlider(cur2, speed2, rez2, dir));

          let rez3 = menuItems[index].position
          let curz = state.scrollL;
          let dif = curz - rez3;
          let speed4 = Math.abs(dif/15)
          cancelAnimationFrame(lineAnim.current);
          lineAnim.current = requestAnimationFrame(() =>  animatLine(speed4,curz,rez3,dif));

          let nextItemW = menuItems[index].iWidth
          cancelAnimationFrame(widthAnimat.current);
          widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));
          setLastWidth(nextItemW)

          setState({
            ...state,
            scrollM:rez,
            isScrollingM: false,
            isScrolling:false,
            scrollX:rez2,
            scrollW:nextItemW,
            scrollL:rez3,
          });

        }
        else if ((sliderIndex - index) === 0) {

          for(let i = 0; i < index; i++) {
            sum += menuItems[i].iWidth;
          }

          rez = Math.round(sum - (compWidth - menuItems[index].iWidth) / 2);

          if(rez < -0){
            rez = 0;
          } else if (rez > 364) {
            rez = 364;
          }

          speed = Math.abs((cur - rez) / 20)


          if((rez - state.scrollM) > 0){
            dir = 'right'
          } else if ((rez - state.scrollM) < 0){
            dir = 'left'
          }

          cancelAnimationFrame(menuAnimation.current);
          menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez, dir));

          setState({
            ...state,
            scrollM:rez,
            isScrollingM: false,
          });


        }


      }

  };

  const animateMenu = (cur, speed, rez, dir) => {
                menuAnimation.current = requestAnimationFrame(() =>
                animateMenu(cur, speed, rez, dir)
              );

              cur = dir === "left" ? cur - speed : cur + speed;

              menuRef.current.scrollLeft = cur.toFixed(2);

              if(Math.round(cur) === rez) {
                  cancelAnimationFrame(menuAnimation.current);
                  menuRef.current.scrollLeft = rez;
                  setLastPos(rez)
                  setDir(0);
              }
  }

  const animateSlider = (cur, speed, rez, dir) => {
            setAnimState(true);

            refAnimation.current = requestAnimationFrame(() =>
              animateSlider(cur, speed, rez, dir)
            );
            cur = dir === "left" ? cur - speed : cur + speed;
            ref.current.scrollLeft = cur.toFixed(2);

            if(Math.round(cur) === rez) {
                cancelAnimationFrame(refAnimation.current);
                ref.current.scrollLeft = rez;
            }
            setAnimState(false);
            setMouseDown(false);
        }

  const onMouseLeaveHandler = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();
    setState({
      ...state,
      isScrollingM: false
    });
  };
  ////////////////////////////////////////////////////

  const handleStart = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();
    setTouchStartMenu(e.touches[0].clientX)
    setState({
      ...state,
      isScrollingM: true,
      clientM: e.touches[0].clientX
    });
  };

  const handleMove = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    let posa = menuRef.current.getBoundingClientRect();

    const { clientM, scrollM, isScrollingM } = state;

    if (isScrollingM === true) {
      let sM = scrollM - e.touches[0].clientX + clientM;
      let cM = e.touches[0].clientX;

      if(e.touches[0].clientX > posa.left &&
         e.touches[0].clientX < posa.right &&
         e.touches[0].clientY > posa.top &&
         e.touches[0].clientY < posa.bottom ) {

          if (sM < 0) {
            sM = 0;
          } else if (sM > 364) {
            sM = 364;
          } else if (sM >= 0 && sM <= 364) {
            menuRef.current.scrollLeft = sM;
            setState({
              ...state,
              scrollM: sM,
              clientM: cM
            });
            if((menuLastPos - sM) > 0) {
              setDir('right')
            } else if ((menuLastPos - sM) < 0)  {
              setDir('left')
            }
          }
        } else {
          handleEnd(e);
        }
    }
  };

  const handleEnd = (e) => {
    if (menuRef && menuRef.current && !menuRef.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    setState({
      ...state,
      isScrollingM:false
    });

    let touchShift = touchStartMenu - state.clientM;

    if (touchShift === 0) {
        let cur = state.scrollM;
        let index = Number(e.target.getAttribute('iso'))
        let dir;
        let dirMenu;
        let rez;
        let sum = 0;
        let speed;
        setSliderIndex(index)


        if((sliderIndex - index) < 0) {

          for(let i = 0; i < index; i++) {
            sum += menuItems[i].iWidth;
          }

          rez = Math.round(sum - (compWidth - menuItems[index].iWidth) / 2);
          if(rez < -0){
            rez = 0;
          } else if (rez > 364) {
            rez = 364;
          }

          dir = 'right'
          if((rez - cur) < 0){
            dirMenu = 'left'
          } else if ((rez - cur) > 0) {
            dirMenu = 'right'
          }


          speed = Math.abs((cur - rez) / 20)

          cancelAnimationFrame(menuAnimation.current);
          menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez, dirMenu));

          let cur2 = state.scrollX;
          let rez2 =  compWidth*index
          let speed2 = (rez2 - cur2) /30

          cancelAnimationFrame(refAnimation.current);
          refAnimation.current = requestAnimationFrame(() =>  animateSlider(cur2, speed2, rez2, dir));

          //////////////////////////
          let rez3 = menuItems[index].position
          let curz = state.scrollL;
          let dif = curz - rez3;
          let speed4 = Math.abs(dif/15)

          cancelAnimationFrame(lineAnim.current);
          lineAnim.current = requestAnimationFrame(() =>  animatLine(speed4,curz,rez3,dif));

          let nextItemW = menuItems[index].iWidth
          cancelAnimationFrame(widthAnimat.current);
          widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));
          setLastWidth(nextItemW)

          setState({
            ...state,
            scrollM:rez,
            isScrollingM: false,
            isScrolling:false,
            scrollX:rez2,
            scrollW:nextItemW,
            scrollL:rez3,
          });
        }
        else if ((sliderIndex - index) > 0) {
          for(let i = 0; i < index; i++) {
            sum += menuItems[i].iWidth;
          }

          rez = Math.round(sum - (compWidth - menuItems[index].iWidth) / 2);

          if(rez < -0){
            rez = 0;
          } else if (rez > 364) {
            rez = 364;
          }

          speed = Math.abs((cur - rez) / 20)

          dir = 'left'
          if((rez - cur) < 0){
            dirMenu = 'left'
          } else if ((rez - cur) > 0) {
            dirMenu = 'right'
          }

          cancelAnimationFrame(menuAnimation.current);
          menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez, dirMenu));

          let cur2 = state.scrollX;
          let rez2 =  compWidth*index
          let speed2 = (cur2 - rez2) / 30
          cancelAnimationFrame(refAnimation.current);
          refAnimation.current = requestAnimationFrame(() =>  animateSlider(cur2, speed2, rez2, dir));

          let rez3 = menuItems[index].position
          let curz = state.scrollL;
          let dif = curz - rez3;
          let speed4 = Math.abs(dif/15)
          cancelAnimationFrame(lineAnim.current);
          lineAnim.current = requestAnimationFrame(() =>  animatLine(speed4,curz,rez3,dif));

          let nextItemW = menuItems[index].iWidth
          cancelAnimationFrame(widthAnimat.current);
          widthAnimat.current = requestAnimationFrame(() =>  widthMove(lastWidth,15, nextItemW));
          setLastWidth(nextItemW)

          setState({
            ...state,
            scrollM:rez,
            isScrollingM: false,
            isScrolling:false,
            scrollX:rez2,
            scrollW:nextItemW,
            scrollL:rez3,
          });

        }
        else if ((sliderIndex - index) === 0) {

          for(let i = 0; i < index; i++) {
            sum += menuItems[i].iWidth;
          }

          rez = Math.round(sum - (compWidth - menuItems[index].iWidth) / 2);

          if(rez < -0){
            rez = 0;
          } else if (rez > 364) {
            rez = 364;
          }

          speed = Math.abs((cur - rez) / 20)


          if((rez - state.scrollM) > 0){
            dir = 'right'
          } else if ((rez - state.scrollM) < 0){
            dir = 'left'
          }

          cancelAnimationFrame(menuAnimation.current);
          menuAnimation.current = requestAnimationFrame(() =>  animateMenu(cur, speed, rez, dir));

          setState({
            ...state,
            scrollM:rez,
            isScrollingM: false,
          });


        }


      }

  };
  /////////////////////////////////////////////////

  const touchStarts = (e) => {
    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }
    firstClientX = e.touches[0].clientX;
    firstClientY = e.touches[0].clientY;

    if(state.isScrolling === false){
        if(animState === false) {

            setMouseDown(true)
            setTouchStart(e.touches[0].clientX);

            setState({
              ...state,
              isScrolling:true,
              clientX: e.touches[0].clientX,
              clientM: e.touches[0].clientX,
              clientL: e.touches[0].clientX,
              clientW: e.touches[0].clientX,
              scrollW:0
            });

        } else if(animState === true) {
          setMouseDown(false)
        }
      }
  };

  const touchMove = (e) => {
    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }


      clientsX = e.touches[0].clientX - firstClientX;
      clientsY = e.touches[0].clientY - firstClientY;

        if (Math.abs(clientsX) > 5 && Math.abs(clientsX) > Math.abs(clientsY)) {
            e.preventDefault();

            let posa = ref.current.getBoundingClientRect();

            if(animState === false) {
              const { clientX, scrollX, isScrolling, scrollM, clientM, clientL, scrollL, scrollW, clientW } = state;

              if (isScrolling === true) {
                    let sX = scrollX - e.touches[0].clientX + clientX;
                    let cX = e.touches[0].clientX;
                    let sM;
                    let cM = e.touches[0].clientX;
                    let sL;
                    let cL = e.touches[0].clientX;
                    let sW;
                    let cW = e.touches[0].clientX;

                    if(e.touches[0].clientX > posa.left && e.touches[0].clientX < posa.right && e.touches[0].clientY > posa.top && e.touches[0].clientY < posa.bottom ) {

                      if (sX < -0) {
                        sX = 0;
                      } else if (sX > 2160) {
                        sX = 2160;
                      }
                      else if(sX >= 0 && sX <= 2160) {

                          ref.current.scrollLeft = sX;


                          if (touchStart - e.touches[0].clientX > 0) {

                              let width = menuItems[sliderIndex + 1].iWidth

                              let sum = 0;
                              for(let i = 0; i < sliderIndex + 1; i++){
                                sum = sum + menuItems[i].iWidth
                              }
                              let position = (sum - (compWidth - width) / 2) - menuLastPos
                              let ratio = (compWidth/position).toFixed(3);

                              sM = scrollM - Math.round(e.touches[0].clientX / ratio) + Math.round(clientM / ratio);

                              ///////////////////Сдвиг влевао////////////////////

                              let nextSlidePos = 0;
                              for(let i = 0; i < sliderIndex + 1; i++){
                                nextSlidePos = nextSlidePos + menuItems[i].iWidth
                              }

                             let nextWidth =  menuItems[sliderIndex + 1].iWidth/2
                             let curW = menuItems[sliderIndex].iWidth
                             let rez;
                             let sumZ = 0;
                             let ratioL;

                             for(let i = 0; i < sliderIndex; i++) {
                               sumZ += menuItems[i].iWidth;
                             }

                             rez = sumZ - (compWidth - menuItems[sliderIndex].iWidth) / 2;

                              if(nextSlidePos <= compWidth/2) {
                                    ratioL = (compWidth/curW).toFixed(3);
                                    sL = scrollL - (e.touches[0].clientX / ratioL) + (clientL / ratioL);
                                    lineRef.current.style.marginLeft = sL + 'px';
                              }

                              else if (nextSlidePos > compWidth/2 && (375 - rez) > nextWidth){
                                let newPos = (compWidth - menuItems[sliderIndex + 1].iWidth)/2;
                                let dif = linePosition - newPos;
                                ratioL = Math.abs(compWidth/dif)

                                if(dif > 0) {
                                  sL = scrollL + (e.touches[0].clientX / ratioL) - (clientL / ratioL);
                                }
                                if(dif < 0) {
                                  sL = scrollL - (e.touches[0].clientX / ratioL) + (clientL / ratioL);
                                }
                                lineRef.current.style.marginLeft = sL + 'px';
                              }

                              else if (nextSlidePos > compWidth/2 && (375 - rez) < nextWidth) {
                                let newPos = compWidth - menuItems[sliderIndex + 1].iWidth
                                let dif = linePosition - newPos;
                                ratioL = Math.abs(compWidth/dif)

                                if(dif > 0) {
                                  sL = scrollL + (e.touches[0].clientX / ratioL) - (clientL / ratioL);
                                }
                                if(dif < 0) {
                                  sL = scrollL - (e.touches[0].clientX / ratioL) + (clientL / ratioL);
                                }

                                lineRef.current.style.marginLeft = sL + 'px';
                              }

                              //////////////////Изменение ширины////////////////////

                              let curWidth = menuItems[sliderIndex].iWidth
                              let nextItemW = menuItems[sliderIndex + 1].iWidth
                              let widthDif = curWidth - nextItemW;
                              let widthRatio = compWidth/ widthDif;

                              if(widthDif > 0) {
                                sW = scrollW - (e.touches[0].clientX/ widthRatio) + (clientW/ widthRatio);
                                lineRef.current.style.width = curWidth - sW + 'px';
                              } else if (widthDif < 0) {
                                sW = scrollW + (e.touches[0].clientX/ widthRatio) - (clientW/ widthRatio);
                                lineRef.current.style.width = curWidth + sW + 'px';
                              }

                              setLastWidth(parseFloat(lineRef.current.style.width))


                              //////////////////////////////////////////////////////

                              if (sM < 0) {
                                sM = 0;
                              } else if (sM > 364) {
                                sM = 364;
                              } else if (sM > 0 && sM <= 364) {
                                  menuRef.current.scrollLeft = sM;
                              }

                              setState({
                                ...state,
                                scrollX: sX,
                                clientX: cX,
                                scrollM: sM,
                                clientM: cM,
                                scrollL: sL,
                                clientL: cL,
                                clientW: cW,
                                scrollW: sW,
                              });

                          }
                          else if (touchStart - e.touches[0].clientX < 0) {

                              let width = menuItems[sliderIndex - 1].iWidth
                              let sum = 0;
                              for(let i = 0; i < sliderIndex - 1; i++){
                                sum = sum + menuItems[i].iWidth
                              }
                              let position = menuLastPos - (sum - (compWidth - width) / 2)
                              let ratio = (compWidth/position).toFixed(3);

                              sM = scrollM - Math.round(e.touches[0].clientX / ratio) + Math.round(clientM / ratio);

                              //////////////////Сдвиг вправо////////////////////

                              let nextSlidePos = 0;
                              for(let i = 0; i < sliderIndex - 1; i++){
                                nextSlidePos = nextSlidePos + menuItems[i].iWidth
                              }

                             // let sumZ = 0;
                             let ratioL;

                             // for(let i = 0; i < sliderIndex; i++) {
                             //   sumZ += menuItems[i].iWidth;
                             // }

                              if(nextSlidePos <= compWidth/2) {
                                    let prevPos =  linePosition - nextSlidePos
                                    ratioL = (compWidth/prevPos).toFixed(3);
                                    sL = scrollL - (e.touches[0].clientX / ratioL) + (clientL / ratioL);
                                    lineRef.current.style.marginLeft = sL + 'px';

                              }

                              else if (nextSlidePos > compWidth/2){
                                let newPos = (compWidth - menuItems[sliderIndex - 1].iWidth)/2;
                                let dif = linePosition - newPos;
                                ratioL = Math.abs(compWidth/dif)

                                if(dif < 0) {
                                  sL = scrollL + (e.touches[0].clientX / ratioL) - (clientL / ratioL);
                                }
                                if(dif > 0) {
                                  sL = scrollL - (e.touches[0].clientX / ratioL) + (clientL / ratioL);
                                }
                                lineRef.current.style.marginLeft = sL + 'px';
                              }



                                let curWidth = menuItems[sliderIndex].iWidth
                                let nextItemW = menuItems[sliderIndex - 1].iWidth
                                let widthDif = curWidth - nextItemW;
                                let widthRatio = compWidth/ widthDif;

                                if(widthDif > 0) {
                                  sW = scrollW + (e.touches[0].clientX/ widthRatio) - (clientW/ widthRatio);
                                  lineRef.current.style.width = curWidth - sW + 'px';
                                } else if (widthDif < 0) {
                                  sW = scrollW - (e.touches[0].clientX/ widthRatio) + (clientW/ widthRatio);
                                  lineRef.current.style.width = curWidth + sW + 'px';
                                }

                                setLastWidth(parseFloat(lineRef.current.style.width))


                              //////////////////////////////////////////////////////

                              if (sM < 0) {
                                sM = 0;
                              }  else if (sM > 364) {
                                sM = 364;
                              }  else if (sM > 0 && sM <= 364) {
                                  menuRef.current.scrollLeft = sM;
                              }
                              setState({
                                ...state,
                                scrollX: sX,
                                clientX: cX,
                                scrollM: sM,
                                clientM: cM,
                                scrollL: sL,
                                clientL: cL,
                                clientW: cW,
                                scrollW: sW,
                              });

                          }
                      }

                    } else {
                      touchEnd(e);
                    }
                 }
            }

         } else if (Math.abs(clientsY) > 5 && Math.abs(clientsY) > Math.abs(clientsX)){
           setState({
             ...state,
             isScrolling:false
           });
         }
  };

  const touchEnd = (e) => {
      if(mouseDown === true) {
        if (ref && ref.current && !ref.current.contains(e.target)) {
          return;
        }
        e.preventDefault();

        if(animState == false) {
          let touchShift = touchStart - state.clientX;
          shiftDirection(touchShift)
          setLinePosition(state.scrollL) // del временно
        }
      }
  };

  ////////////////////////////////////////////////////

  useEffect(() => {
    if(ref.current){
      ref.current.addEventListener("mousedown", onMouseDown);
      ref.current.addEventListener("mouseup", onMouseUp);
      ref.current.addEventListener("mousemove", onMouseMove);
      ref.current.addEventListener("mouseleave", onMouseLeave);
      ref.current.addEventListener("mousedown", onMouseDownHandler);
      ref.current.addEventListener("mouseup", onMouseUpHandler);
      ref.current.addEventListener("mousemove", onMouseMoveHandler);
      ref.current.addEventListener("mouseleave", onMouseLeaveHandler);

      ref.current.addEventListener("touchstart", touchStarts);
      ref.current.addEventListener("touchmove", touchMove);
      ref.current.addEventListener("touchend", touchEnd);
    }

    document.addEventListener("touchstart", handleStart);
    document.addEventListener("touchmove", handleMove);
    document.addEventListener("touchend", handleEnd);

    // document.addEventListener("scroll", stopHandler);

    return () => {
      if(ref.current){
        ref.current.removeEventListener("mousedown", onMouseDown);
        ref.current.removeEventListener("mouseup", onMouseUp);
        ref.current.removeEventListener("mousemove", onMouseMove);
        ref.current.removeEventListener("mouseleave", onMouseLeave);
        ref.current.removeEventListener("mousedown", onMouseDownHandler);
        ref.current.removeEventListener("mouseup", onMouseUpHandler);
        ref.current.removeEventListener("mousemove", onMouseMoveHandler);
        ref.current.removeEventListener("mouseleave", onMouseLeaveHandler);

        ref.current.removeEventListener("touchstart", touchStarts);
        ref.current.removeEventListener("touchmove", touchMove);
        ref.current.removeEventListener("touchend", touchEnd);
      }

      document.removeEventListener("touchstart", handleStart);
      document.removeEventListener("touchmove", handleMove);
      document.removeEventListener("touchend", handleEnd);

      // document.removeEventListener("scroll", stopHandler);

    };
  });
  useEffect(() => {
    if(ref.current ===true){
      refAnimation.current = requestAnimationFrame(() => animate(0, 0, 0));
    }
    return () => {
      cancelAnimationFrame(refAnimation.current);
    };
  }, []);
  useEffect(() => {
    if(ref.current ===true){
        sliderAnimation.current = requestAnimationFrame(() => animateSlider(0, 0, 0));
    }
    return () => {
      cancelAnimationFrame(sliderAnimation.current);
    };
  }, []);
  useEffect(() => {
    if(menuRef.current ===true){
    menuAnimation.current = requestAnimationFrame(() => animateMenu(0, 0, 0));
    }
    return () => {
      cancelAnimationFrame(menuAnimation.current);
    };
  }, []);
  useEffect(() => {
    if(lineRef.current === true){
    lineAnim.current = requestAnimationFrame(() => animateMenu(0, 0, 0));
    }
    return () => {
      cancelAnimationFrame(lineAnim.current);
    };
  }, []);
  useEffect(() => {
    if(lineRef.current === true){
      widthAnimat.current = requestAnimationFrame(() => animateMenu(0, 0, 0));
    }
    return () => {
      cancelAnimationFrame(widthAnimat.current);
    };
  }, []);


  const menuBar = menuItems.map((m, i) => {
    return (
      <div key={i} iso={i} className={classes.menu_item}>
        {m.menuTitle}
      </div>
    );
  });

  // console.log(this.props.match.params.id)
  const [locat, setLocate] = useState();

  useEffect(() => {
    if(ref.current){
      ref.current.scrollLeft = 0;
      menuRef.current.scrollLeft = 0
    }
    let id = data[match.params.id]

    setLocate(match.params.id);
    //   return <Redirect to="/" />
  }, [])



    if(data[match.params.id]) {
      const scrollBar = data[match.params.id].map((v, i) => {

        return (
        <div key={i} className={classes.item}>
          {v.imgArray.map((j, id) =>{
            return <img key={id} className={classes.img} src={`/images/kombo/${locat}/${j.img}`}/>
          })}
        </div>
        );
    });
      return (
    <div className={classes.wrapper}>
      <div
      className={classes.charPage}
      >

        <div
          className={classes.charMenu}
          ref={menuRef}
          onMouseDown={onMouseDownHandler}
          onMouseUp={onMouseUpHandler}
          onMouseMove={onMouseMoveHandler}
          onMouseLeave={onMouseLeaveHandler}

          onTouchStart={handleStart}
          onTouchMove={handleMove}
          onTouchEnd={handleEnd}
          >
          {menuBar}
        </div>


        <div className={classes.lineWrap}>
          <div
          className={classes.activeLine}
          ref={lineRef}
          ></div>
        </div>


        <div
          className={classes.items}
          ref={ref}
        >
          {scrollBar}
        </div>
      </div>
    </div>

  );
} else {
  headerSw('Персонажи')
  return <Redirect to="/characters" />
}
};

export default Scrollable;
