import {React, Component} from 'react'
import classes from './App.module.scss'

import Menu from './components/menu/menu'
import Header from './components/menu/header'
import MainPage from './pages/main/main'
import Error from './pages/404/404'
import Characters from './pages/characters/characters'
import Settings from './pages/settings/settings'
import Login from './components/login/login_form'
import Sidebar from './components/sidebar/sidebar'


import {Route,Switch,Redirect} from 'react-router-dom'
import {connect} from 'react-redux'
import InfoPopup from './components/popup-info/popup-info'
import CharacterPage from './pages/character-page/character-page'
import {autoLogin} from './store/actions/auth';

import Logout from './components/Logout/Logout'

const charData = [
    {name:'Фуджин',imgName:'fujin.jpg',charId:'fujin',id:1, order:1},
    {name:'Скорпион',imgName:'scorpion.jpg',charId:'scorpion', id:2, order:2},
    {name:'Кабал',imgName:'kabal.jpg',charId:'kabal', id:3, order: 3},
    {name:'Рэйдан', imgName:'raiden.jpg',charId:'raiden', id:4 , order:4},
    {name:'Саб-Зиро',imgName:'subzero.jpg',charId:'subzero', id:5 , order:5},
    {name:'Кунг Лао',imgName:'kunglao.jpg',charId:'kunglao', id:6, order:6}

]


class App extends Component {

    state = {
        showPopup: false,
        titleName:'Главная',
        titleChar:'',
        titleState: '',
        language:''
    };


    componentDidMount() {

      this.props.autoLogin()



      let langState = localStorage.getItem('language');
      let titleStateKey = localStorage.getItem('titleState');
      this.setState({
        titleState: titleStateKey || "true",
        language: langState || "ru"
      });

        if(window.location.pathname === '/'){
           localStorage.setItem('titleName', 'Главная');
           this.setState({titleState: 'true'});
       } else if (window.location.pathname === '/characters') {
            localStorage.setItem('titleName', 'Персонажи');
             this.setState({titleState: true});
        } else if (window.location.pathname === '/settings') {
            localStorage.setItem('titleName', 'Настройки');
            this.setState({titleState: true});
        }

        charData.forEach((item) => {
            if(window.location.pathname === '/characters/' + item.charId){
                this.setState({titleChar: item.name});
            }
        });


        let titleNameKey = localStorage.getItem('titleName');
         this.setState({titleName: titleNameKey});

        // let titleCharKey = localStorage.getItem('titleChar');
        //   this.setState({titleChar: titleCharKey});




     }


    onBtnClick = () =>  {
        this.setState({
            showPopup: true,
        });
    }

    offBtnClick = () =>  {
        this.setState({
            showPopup: false,
        });
    }

    headerSwitch = (titles) => {
            if(this.state.titleName === titles && this.state.titleState === 'true'){
              return false
            }

                this.setState({
                    titleName:titles,
                    titleState:'true',
                });
                localStorage.setItem('titleState', 'true');
        }

    characterSwitch = (titles, id) => {
            this.setState({
                titleChar:titles,
                titleState:'false',
            });
            localStorage.setItem('titleChar', titles);
            localStorage.setItem('titleState', 'false');
            // localStorage.setItem('characterId', id);
        }

    switchOnHeader = () => {
            this.setState({
                titleState:'true',
            })
            localStorage.setItem('titleState', 'true');

    }

    languageHandler = (lang) => {
      this.setState({
          language:lang,
      })
      localStorage.setItem('language', lang);
    }

    // mainDropHandler = (e,item) => {
    //   setCardList(filter.map(c =>{
    //     if(c.id === item.id){
    //       return {...c, order: currentCard.order}
    //     }
    //     if (c.id === currentCard.id) {
    //       return {...c, order: item.order}
    //     }
    //     return c
    //   }))
    //   e.target.style.opacity = '1'
    // }

render(){

  let routes = (
    <Switch>
      <Route path="/" component={Login}/>
      <Redirect to="/" />
    </Switch>
  )

  if(this.props.isAuhtenticated) {
    routes = (
      <div>
        <Sidebar/>
        <Header
        titleState={this.state.titleState}
        titleChar={this.state.titleChar}
        titleName={this.state.titleName}
        showPopup={this.onBtnClick}
        switchOnHeader={this.switchOnHeader}/>

        <Switch>
              <Route path="/logout" component={Logout}/>
              <Route path="/" exact component={MainPage}/>

              {/*<Route headerSw={this.headerSw} path="/characters/" exact component={Characters}/>*/}
              <Route
              path="/characters"
              exact
              component={()=><Characters
                  headerSw={this.characterSwitch}
                  sendValue={this.sendValue}
                  charData={charData}
                  />}
              />
              {/*<Route  path="/characters/" exact/>
                  <Characters headerSw={this.headerSw}/>
              <Route/>*/}
              <Route
                  exect
                  path="/characters/:id"
                  render={(props) => <CharacterPage {...props} headerSw={this.headerSwitch} />}
              />
              <Route
              exect
                path="/settings"
                component={
                  ()=><Settings languageHandler={this.languageHandler} languages={this.state.language}/>}
                />
              {/*<Route exect insecure component={Error} >*/}
              <Route path="/404" component={()=><Error headerSw={this.headerSwitch}/>} />
              <Redirect to="/404" />
        </Switch>

        <Menu headerSwitch={this.headerSwitch}/>


        <InfoPopup popupState={this.state.showPopup} popupOff={this.offBtnClick}/>
      </div>
    )
  }


    return (
        <div className={classes.body}>
                {routes}
        </div>
    )
}

};

function mapStateToProps(state) {
  return {
    isAuhtenticated: !!state.auth.token
  }
}

function mapDispatchToProps(dispatch){
  return {
    autoLogin: () => dispatch(autoLogin())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)

